/* ------------------------------------------------------------ *\
	Intro
\* ------------------------------------------------------------ */

.intro { font-weight: 300; font-size: 22px; color: $blue; line-height: 1.52; }

.intro .intro_inner { position: relative; display: flex; flex-wrap: wrap; justify-content: flex-end; }

.intro .intro_image { position: relative; -webkit-box-flex: 0; -ms-flex: 0 0 52%; flex: 0 0 52%; max-width: 52%; padding-top: 59%; }

.intro .intro_image svg { position: absolute; top: 0; left: 0; width: 100%; height: auto; }

.intro .intro_content { position: absolute; top: 50%; left: 0; width: 48%; padding: 50px 92px 50px 0; margin-top: -28px; transform: translateY(-50%); }

.intro .intro_entry { max-width: 443px; margin-left: auto; }

.intro .intro_entry p { margin-bottom: 56px; }

.intro .intro_title { margin-bottom: 24px; color: $blue2; }

.intro .intro_subtitle { margin-bottom: 24px; font-family: $font-Volkhov; font-weight: 400; font-style: italic; font-size: 25px; color: $gray }

.intro .intro_actions { display: -webkit-box; display: -ms-flexbox; display: flex; -ms-flex-wrap: wrap; flex-wrap: wrap; margin: 0 -10px; }

.intro .intro_actions a { margin: 0 10px 10px; }

.intro .intro_actions .btn-primary { box-shadow: 0 12px 18px 0 rgba(0, 0, 0, .11) }

/*  Intro Service  */

.intro-service {  }

.intro-service .intro_image { flex: 0 0 47.3%; max-width: 47.3%; padding-top: 0; }

.intro-service .intro_image .introImageText { position: absolute; bottom: 77px; left: -170px; }

.intro-service .intro_content { padding: 144px 40px 50px 0; }

.intro-service .intro_title { letter-spacing: -.04em; }

.intro-service .intro_entry { max-width: 498px; }

.intro-service .intro_entry p { margin-bottom: 100px; }

/*  Intro Service Secondary  */
.intro-serviceSecondary .intro_inner { display: block; padding: 241px 0 25px; }

.intro-serviceSecondary .intro_image { position: relative; max-width: 100%; padding-top: 0; margin-top: -45px; text-align: center; }

.intro-serviceSecondary .intro_image .introImageText { position: absolute; top: 139px; left: 50%; margin-left: -79px; transform: translateY(-50%); }

.intro-serviceSecondary .intro_content { position: relative; top: 0; width: 100%; padding: 0; transform: translateY(0); }

.intro-serviceSecondary .intro_entry { max-width: 100%; padding: 0 61px; }

.intro-serviceSecondary .intro_entry h1 { font-size: 48px; }

.intro-serviceSecondary .intro_subtitle { margin-bottom: 52px; }

/*  Intro Service Alt  */
.intro-serviceAlt {  }

.intro-serviceAlt .intro_inner { padding: 110px 0 50px; }

.intro-serviceAlt .intro_image { flex: 0 0 58%; max-width: 58%; padding-top: 0; }

.intro-serviceAlt .intro_content { width: 50.5%; padding: 50px 0; margin-top: 41px; }

.intro-serviceAlt .intro_entry { max-width: 552px; font-size: 19px; line-height: 1.8; color: $blue; }

.intro-serviceAlt .intro_entry h1 { font-size: 48px; }

.intro-serviceAlt .intro_entry p { margin-bottom: 70px; }

.intro-serviceAlt .intro_title { margin-bottom: 22px; }

.intro-serviceAlt .intro_subtitle { margin-bottom: 20px; line-height: 1.5; }

$breakpoint-min-lg-desktop {
	.intro .intro_content { top: 0; width: 59%; padding: 250px 92px; transform: none; }
	.intro .intro_entry { max-width: 60%; margin-right: 0; }

	.intro-serviceAlt .intro_content { width: 50%; }
}

/* Small Desktop */

$breakpoint-small-desktop {
	.intro .intro_content { padding-right: 0; }

	.intro-service .intro_content { padding-left: 30px; }
}

/* Tablet Portrait */

$breakpoint-tablet-portrait {
	.intro { font-size: 14px; }

	.intro .intro_image { flex: 0 0 66%; max-width: 66%; width: 66%; padding-top: 74%; pointer-events: none; }

	.intro .intro_content { width: 50%; padding: 144px 34px 46px; }

	.intro .intro_title { margin-bottom: 12px; }

	.intro .intro_subtitle { margin-bottom: 19px; font-size: 18px; }

	.intro .intro_entry p { margin-bottom: 18px; }

	.intro .intro_actions { display: block; margin: 0; }

	.intro .intro_actions a { margin: 0; }

	.intro .intro_actions img { display: block; margin-bottom: 46px; }

	.intro .intro_actions .btn { min-width: 0; height: 44px; padding: 0 20px; line-height: 44px; font-size: 14px; }

	.intro-service .intro_image { flex: 0 0 50%; max-width: 50%; width: 50%; padding-top: 0; }

	.intro-serviceSecondary .intro_inner { padding: 130px 0 25px; }

	.intro-serviceSecondary .intro_image { max-width: 100%; width: 100%; padding-top: 0; margin-top: 0; }

	.intro-serviceSecondary .intro_image .introImageText { top: 20px; }

	.intro-serviceSecondary .intro_content { width: 100%; padding: 50px 0; }

	.intro-serviceAlt .intro_image { flex: 0 0 55%; max-width: 55%; width: 55%; padding-top: 0; }

	.intro-serviceAlt .intro_content { flex: 0 0 45%; max-width: 45%; width: 45%; position: relative; top: 0; padding: 0; margin: 0; transform: translateY(0); }
}

/* Mobile */

$breakpoint-mobile {
	.intro { font-size: 22px; }

	.intro .intro_image { flex: 0 0 80%; max-width: 80%; width: 80%; padding-top: 90%; margin-bottom: 44px; }

	.intro .intro_content { order: 1; flex: 0 0 100%; max-width: 100%; position: relative; top: 0; left: 0; width: 100%; padding: 50px 30px; margin-top: 0; transform: none; }

	.intro .intro_title { margin-bottom: 22px; line-height: 1.17; }

	.intro .intro_subtitle { margin-bottom: 26px; font-size: 28px; }

	.intro .intro_entry { max-width: none; margin-left: 0; text-align: center; }

	.intro .intro_entry p { margin-bottom: 0; }

	.intro .intro_actions img { margin: -3px 0 0; }

	.intro .intro_actions .btn { height: 70px; padding: 0 40px; margin-top: 38px; line-height: 70px; font-size: 24px; }

	.intro-service .intro_image { flex: 0 0 50%; max-width: 50%; width: 50%; padding-top: 0; }

	.intro-serviceSecondary .intro_inner { padding: 75px 0; }

	.intro-serviceSecondary .intro_image { max-width: 100%; width: 100%; padding-top: 0; }

	.intro-serviceSecondary .intro_content { margin-bottom: 70px; }

	.intro-serviceAlt .intro_image { flex: 0 0 100%; max-width: 100%; width: 100%; padding-top: 0; }
}

$breakpoint-mobile-portrait {
	.intro { font-size: 14px; color: $gray6; }

	.intro .intro_image { flex: 0 0 70%; max-width: 70%; width: 70%; padding-top: 79%; margin-bottom: 0; }

	.intro .intro_content { padding: 20px 30px; }

	.intro .intro_title { margin-bottom: 11px; color: $black; }

	.intro .intro_subtitle { margin-bottom: 18px; font-size: 18px; }

	.intro .intro_actions img { max-width: 82%; margin: -4px auto 22px; }

	.intro .intro_actions .btn { height: 44px; padding: 0 25px; line-height: 44px; font-size: 15px; }

	.intro-service .intro_image { flex: 0 0 60%; max-width: 60%; width: 60%; padding-top: 0; }

	.intro-service .intro_image .introImageText { display: none; }

	.intro-serviceSecondary .intro_image { max-width: 100%; width: 100%; padding-top: 0; }

	.intro-serviceSecondary .intro_image .introImageText { top: -20px; }

	.intro-serviceSecondary .intro_content { padding: 20px 0; margin-bottom: 100px; }

	.intro-serviceSecondary .intro_entry { padding: 0; }

	.intro-serviceAlt .intro_image { flex: 0 0 100%; max-width: 100%; width: 100%; padding-top: 0; }

	.intro-serviceAlt .intro_content { padding: 20px 0; }

	.intro-serviceAlt .intro_entry h1 { font-size: 46px; }
}

/* ------------------------------------------------------------ *\
	Intro Service Tertiary
\* ------------------------------------------------------------ */

