/* ------------------------------------------------------------ *\
	Intro Tertiary
\* ------------------------------------------------------------ */

.introTertiary { position: relative; background-size: cover; background-position: center; background-repeat: no-repeat; }

.introTertiary:after { content: ''; position: absolute; top: 0; left: 0; width: 100%; height: 100%; background: linear-gradient(to right, rgba(0, 0, 0, 1) 0%, rgba(0, 0, 0, .01) 100%); opacity: .5; }

.introTertiary .intro_inner { position: relative; z-index: 5; padding: 250px 5px 319px; }

.introTertiary .intro_title { font-weight: 700; color: $white; }

.introTertiary .intro_subtitle { margin-bottom: 24px; font-family: $font-Volkhov; font-style: italic; font-weight: 400; color: $white; }

.introTertiary .intro_entry h2 { font-size: 44px; }

.introTertiary .intro_entry h6 { font-size: 16px; }

.introTertiary .intro_image { position: absolute; bottom: -10px; left: 50%; display: inline-block; padding: 13px 32px; border-radius: 4px; background-color: $white; transform: translateX(-50%); }

