	.introTertiary .intro_entry h2 { font-size: 30px; }

	.introTertiary .intro_image { bottom: -30px; padding: 10px 20px; transform: scale(.7) translateX(-70%); }
}

/* ------------------------------------------------------------ *\
	Intro Quaternary
\* ------------------------------------------------------------ */

.introQuaternary { padding: 126px 0; }

.introQuaternary .intro_inner { display: flex; align-items: center; margin: 0 -75px; }

.introQuaternary .intro_content { flex: 0 0 51.8%; max-width: 51.8%; padding: 0 75px; margin-bottom: 153px; }

.introQuaternary .intro_image { position: relative; flex: 0 0 48.2%; max-width: 48.2%; padding: 0 75px; }

.introQuaternary .intro_image .introImageText { position: absolute; bottom: -25px; left: -122px; }

.introQuaternary .intro_entry { max-width: 537px; padding: 0 20px; margin-left: auto; font-size: 22px; line-height: 1.55; color: $blue; }

.introQuaternary .intro_entry h1 { font-size: 48px; }

.introQuaternary .intro_entry h4 { font-size: 25px; }

.introQuaternary .intro_title { margin-bottom: 24px; font-weight: 700; color: $blue2; }

.introQuaternary .intro_subtitle { margin-bottom: 23px; font-family: $font-Volkhov; font-style: italic; font-weight: 400; color: $gray13; }

/*  Intro Quaternary Alt  */

.introQuaternary-alt { position: relative; padding: 270px 0 160px; }

.introQuaternary-alt:before { content: attr(data-title); position: absolute; top: 50%; left: 0; z-index: -5; font-weight: 800; font-size: 331px; line-height: 1; color: $blue14; white-space: nowrap; pointer-events: none; opacity: .5; transform: translateY(-50%); }

.introQuaternary-alt .intro_content { margin-bottom: 0; }

.introQuaternary-alt .intro_title { position: relative; margin-bottom: 14px; }

.introQuaternary-alt .intro_subtitle { margin-bottom: 43px; }

