.introQuaternary-altSecondary .intro_title { margin-bottom: 24px; }

.introQuaternary-altSecondary .intro_subtitle { margin-bottom: 23px; }

.introQuaternary-approach { padding: 236px 0 60px; }

.introQuaternary-approach .intro_inner { display: block; padding: 0 90px; }

.introQuaternary-approach .intro_content { max-width: 100%; padding: 0; }

.introQuaternary-approach .intro_entry { max-width: 830px; margin: 0; font-size: 27px; line-height: 1.57; }

.introQuaternary-approach .intro_entry h1 { font-size: 60px; }

.introQuaternary-approach .intro_entry h4 { font-size: 33px; }

.introQuaternary-approach .intro_title { margin-bottom: 22px; }

