	.introQuaternary-alt .intro_entry { max-width: 100%; }

	.introQuaternary-approach .intro_inner { padding: 0; }

	.introQuaternary-approach .intro_entry { max-width: 830px; margin: 0; font-size: 18px; line-height: 1.57; }

	.introQuaternary-approach .intro_entry h1 { font-size: 34px; }

	.introQuaternary-approach .intro_entry h4 { font-size: 20px; }
}

/* Mobile */

$breakpoint-mobile {
	.introQuaternary .intro_inner { display: block; margin: 0; }

	.introQuaternary .intro_content { max-width: 100%; padding: 0; margin-bottom: 50px; }

	.introQuaternary .intro_image { max-width: 100%; padding: 0 20px; }

